import './article.css';
//cart
import Cart from '../images/boxOne.png';


  let ok=()=>{
    // if(window.innerWidth>1000){
      let append= document.querySelector('body > main > article > div:nth-child(5) > img:nth-child(1)');
        let append2= document.querySelector('body > main > article > div:nth-child(5) > img');
        let append3= document.querySelector('body > main > article > div:nth-child(4) > img');
      document.querySelector('.cart').prepend(append||append2||append3);
    // }
  }
  function remove(){
      // if(window.innerWidth>1000){
      // const newLi = document.createElement('p');
      let removvE= document.querySelector('body > main > article > div:nth-child(4) > img:nth-child(1)');
      let removv= document.querySelector('body > main > article > div:nth-child(4) > img');
      document.querySelector('.cart').removeChild(removvE||removv);
    // }
  }
const consoleLog=()=>{
  // if(window.innerWidth>1000){
    let append= document.querySelector('body > main > article > div:nth-child(4) > img:nth-child(1)');

    document.querySelector('.cart').append(append);
  // }
}
function Create(){
  console.log("Create");
  let ImG=document.createElement("img");
  document.querySelector('.cart').prepend(ImG);
  ImG.setAttribute('src',Cart);
}
// consoleLog();
